import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { getQueryParams } from 'utils/urlUtil';
import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getClasss, deleteClass } from 'services/class';
import FormSearchClasses from './FormSearchClasses';

class ListClassesPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteClass(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload, paramSearch } = this.state;
    const columns = [
      {
        title: 'Tên',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Số SV',
        dataIndex: 'noOfStudent',
        key: 'noOfStudent',
      },
      {
        title: 'Niên khóa',
        dataIndex: 'courseYearLabel',
        key: 'courseYearLabel',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'facultyName',
        key: 'facultyName',
      },
      {
        title: 'CN',
        dataIndex: 'studyFieldName',
        key: 'studyFieldName',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#classes/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="DS lớp học">
        <CartHeadAction>
          <BtnAddNew href="#classes/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchClasses />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getClasss}
              paramSearch={paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassesPage />
  </Page>
);



